.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  font-weight: 700;

  &__top {
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  /* ми не пишемо це на блок navб тому що це протиречить БЭМ - блок nav може десь перевикористовуватись */
  &__nav {
    margin-bottom: 48px;
  }

  &__content {
    @include content-padding-inline;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 22.4px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content; /* ширина не буде розтягуватись до максимального розміру батьківскького елементу */
    padding-bottom: 4px;
    border-bottom: 1px solid #1b2129;

    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
  }
}
